.section6-ziple-about {
    width: 80%;
    margin: 0 auto;
    max-width: 1300px;
    min-width: 325px;

    .section6-ziple-about-headline {

        font-size:3rem;
        text-align:left;
        padding:200px  0 70px 0;
        color:#444444;

        h2 {
            font-size:1.8rem;
            color:#5882FA;
        }
    }

    .section6-ziple-about-card {
        list-style: none;
        display: flex;
        img {
            width:50%;
        }
        li {
            width:50%;
            text-align:center;
            display: flex;
            flex-direction: column;
            justify-content: center;

            .section6-ziple-about-subtxt {
                color:#5882FA;
                font-size:1.5rem;
                margin:0;
            }

            .section6-ziple-about-txt {
                font-size:2.5rem;
                word-break:keep-all;
            }

            p {
                font-size:1rem;
                color:#444444;
                font-weight:500;
            }

        }
    }
}

