.section4-ziple-tech {
    width:80%;
    margin:0 auto;
    max-width: 1300px;
    min-width: 325px;
  
    .section4-ziple-tech-headline {
        font-size:3rem;
        text-align:left;
        padding:200px  0 70px 0;
        color:#444444;

        h2 {
            font-size:1.8rem;
            color:#5882FA;
        }
    }

    .section4-ziple-tech-container {
        margin-top:80px;
        padding:0;
        list-style:none;
        display:flex;
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;
        .section4-ziple-tech-card {
           width:50%;
          
            .section4-ziple-tech-title {
                font-size:2rem;
                color:#5d626d;
            }
            .section4-ziple-tech-list {
                color:#9da4ad;
                font-size:1rem;
                font-weight:bold;
                display:flex;
                flex-direction: column;
                span {
                    padding-bottom:5px;
                }
            }
        }
    }
}