.section5-ziple-portfolio {
    width:80%;
    margin:0 auto;
    max-width: 1300px;
    min-width: 325px;
    .section5-ziple-tech-headline {
        text-align:center;
        font-weight:bold;
        font-size:3rem;
        text-align:center;
        padding-top:250px;
        color:#444444;
            
        h2 {
            font-size:1.5rem;
            color:#444444;
            font-weight: 400;
        }
    }

    .section5-ziple-portfolio-container {
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        padding: 0;
        li {
            width:500px;
            border-radius:10px;
            overflow:hidden;
            margin-top:40px;
            cursor:pointer;
            img {
                width:100%;
                height:100%;
            }
        }
    }
   
            
        
    
}