.footer-ziple {
    width:100vw;
    height:500px;
    background:#F9FAFB;
    margin-top:200px;
    .footer-ziple-inner {
        width:80%;
        margin:0 auto;
        max-width: 1300px;
        min-width: 325px;

        // 주소
        p.footer-ziple-address {
            

            .footer-ziple-address-items {
                list-style:none;
                display:flex;
                padding:0;
                li {
                    width:33%;
                    border-right:1px solid lightgray;
                    p {
                        color:#8b95a1;
                        font-size:0.8rem;
                        padding:0 0 20px;
                        display:flex;
                        flex-direction:column;
                        strong {
                            padding-top:2px;
                        }
                    }
                }
            }
        }

        p.footer-ziple-customer {
            list-style:none;
            display:flex;
            padding:0;
        }
    }
}