.section7-ziple-pf-request {
    width: 80%;
    margin: 0 auto;
    max-width: 1300px;
    min-width: 325px;
    .section7-ziple-tech-headline {

        font-size: 3rem;
        text-align: left;
        padding: 200px 0 70px 0;
        color: #444444;

        h2 {
            font-size: 1.8rem;
            color: #5882FA;
        }
    }

    .section7-ziple-spread-box {
        width: 100%;
        height:600px;
        position: relative;
        margin-top:100px;
        .section7-ziple-spread-left {
            position:absolute;
            width:30%;
            height:100%;
            left:0;
            top:0;
            background:#ffffff;
            z-index:100;
            transition:all 1s;
        }
        .section7-ziple-spread-right {
            position:absolute;
            width:30%;
            height:100%;
            right:0;
            top:0;
            background:#ffffff;
            z-index:100;
            transition:all 1s;
        }

        .section7-ziple-spread-txt {
            position:absolute !important;
            top:50% !important;
            left:50% !important;
            transform:translate(-50%, -50%) !important;
            font-size:2rem;
            font-weight:bold;
            z-index:200;
            color:#ffffff;
        }

        .cancle {
            width:0;
        }

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius:10px;
        }
        img.spread {
            width: 100%; 
        }
    }



}